/*/commande_inc/*/
.container_input_text{
   margin-top: 2rem;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}
.container_input_text2{
   margin-top: 4rem;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}
.container_input_text_logine{
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
}
.ligne_inc{
   color: white;
   display: flex;
   align-items: flex-end;
   margin-right: 3rem;
}
.gauche_ligne{
   margin-bottom: 0.2rem;
   width:105px;
   display: flex;
   justify-content: space-between;
   align-items: center;
}
.container_recherche_inc{
   width: 10vw;
   position: relative;
   height: 40px;
   overflow: hidden;
   margin-top: 15px;
   margin-left: 1rem;
}
.fa-search{
  color: #0f63d6;
}
.container_input_inc{
background-color: #0f62d600;
font-weight: bold;
margin-left: 35px;
}

.container_recherche_inc input{
  width: 100%;
  height: 100%;
  color: white;
  border: none;
  outline: none;
} 
.container_recherche_inc label {
  position: absolute;
  bottom: 1px;
  left: 0%;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1.5px solid white;   
}
.container_recherche_inc label::after{
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0px;
  bottom: -1px;
  border-bottom: 2px solid white;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
.recontent-span_inc{
  position: absolute;
  bottom: 1px;
  left: 3px;
  transition: all 0.3s ease;
  color: white;
}

.container_recherche_inc input:focus +.recherche_name_inc .recontent-span_inc , 
.container_recherche_inc input:valid + .recherche_name_inc .recontent-span_inc {
  transform: translateY(-50%) translateX(-30%);
  font-size: 0.8em;
  color: white;
}

.container_recherche_inc input:focus +.recherche_name_inc::after ,.container_recherche_inc input:valid + .recherche_name_inc::after{
  transform: translateX(0%);
}

@keyframes fanao {
  50%{ transform: scale(1.015, 1.05);}
  100%{ transform: scale(1, 1);}
}

.btn_envoyer_inc{
  margin-top: 3rem;
  width: 110px;
  padding: 1rem;
  color: white;
  border: solid white 1px;
  border-radius: 3px;
  display: flex;
  justify-content: center;  
}
.btn_envoyer_inc:hover{
  margin-top: 3rem;
  width: 110px;
  padding: 1rem;
  color: white;
  border: solid white 1px;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  animation-name: fanao;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  background-color: #00398a;
}
.btn_text_envoyer_inc{
  margin-right: 0.5rem;
}

