.container_registre_gmao{
    top: 8vh;
    position: absolute;
    width: 100%;
}

.contine_registre_bar{
    margin-bottom: 3rem;
    background-color: white;
    display: flex;
}

.titre_img_registre{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 8vw;
  }

  .deco_bar_registre{
    height: 8vh;
    margin-left: 2vw;
  }
  .titre_registre{
    font-size: 2vw;
    font-weight: 500;
    text-transform: uppercase;
    color: #0f63d6;
    font-family: 'Open Sans', sans-serif;
    height: 5rem;
    display: flex;
    align-items: center;
  }
  .registre_pass_oublier{
      margin-top: 2rem;
      color: white;
  }
  .registre_pass_oublier:hover{
    font-weight: bold;
  }
  .soratra{
    height: 10vw;
    width: 10vw;
    position: absolute;
    bottom: 20rem;
    left: 7rem;
  }
  
  @media screen and (max-width : 1000px){
    .soratra{
      display: none;
    }
  }
  @media screen and (max-width : 600px){

    .container_registre_gmao{
      position: absolute;
      width: 100%;
      top: 12vh;
  }
  
