.container_accueil_parent_fournisseur{
    display: flex;
    flex-direction: column;
    margin-top: 8vh;
    position: absolute;
    width: 100%;
    height: 100%;
}

/*/ DEBUT PARTIE COMMUN /*/
.container_bas_nav{
    display: flex;
    justify-content: space-around;
    background-color: white;
    margin-top: 4rem;
    align-items: center;
 }
 .titre_F{
     font-size: 2vw;
     font-weight: 500;
     text-transform: uppercase;
     color: #0f63d6;
     font-family: 'Open Sans', sans-serif;
     height: 5rem;
     display: flex;
     align-items: center;
 }
 .container_recherche{
    width: 15vw;
    position: relative;
    height: 60px;
    overflow: hidden;
    border-bottom: solid 1px #0f62d67a;
}
.fa-search{
   color: #0f63d6;
}

.container_input{
 background-color: #0f62d600;
 font-weight: bold;
 margin-left: 35px;
 font-size: 1.3em;
}

.container_recherche input{
   width: 100%;
   height: 100%;
   color: #0f63d6;
   border: none;
   outline: none;
} 

.container_recherche label {
   position: absolute;
   bottom: -3px;
   left: 0%;
   width: 100%;
   height: 100%;
   pointer-events: none;
}

.container_recherche label::after{
   content: "";
   position: absolute;
   height: 100%;
   width: 100%;
   left: 0px;
   bottom: -1px;
   border-bottom: 2px solid #0f63d6;
   transform: translateX(-100%);
   transition: transform 0.3s ease;
}

.recontent-span{
   position: absolute;
   bottom: 5px;
   left: 10px;
   transition: all 0.3s ease;
   color: rgb(0, 0, 0,0.5);
}

.container_recherche input:focus +.recherche_name .recontent-span , 
.container_recherche input:valid + .recherche_name .recontent-span {
   transform: translateY(-70%) translateX(-30%);
   font-size: 0.8em;
   color: rgb(20, 153, 109);
}

.container_recherche input:focus +.recherche_name::after ,.container_recherche input:valid + .recherche_name::after{
   transform: translateX(0%);
}
.container_fournisseurs{
    width: 70vw;
    margin: auto;
    margin-top: 2rem;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    align-items: center;   
}

.container_fournisseur_footer{
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
  }
  
  .footer_image_fond_fournisseurs{
    height: 15vh;
    width: 100%;
    position: absolute;
    bottom: 0rem;
    
  }
  .titre_img_fournisseur{
    display: flex;
    justify-content: center;
    align-items: center;
  }

/*/-------------------------------FIN PARTIE COMMUN -------------------------------- /*/

.container_box_parent_fournisseur{
    width: 350px;
    height: 350px;
    margin: 1rem;
    border-radius: 5px;
    background-image: url("../images/fond_back_2.png");
    background-repeat: no-repeat;
    background-size: cover;
    border: 0.5px solid white;
    position: relative;
  }
  .container_box_parent_fournisseur_click{
      width: 350px;
      height: 50px;
      margin: 1rem;
      border-radius: 5px;
      background-image: url("../images/fond_back_2.png");
      background-repeat: no-repeat;
      background-size: cover;
      border: 0.5px solid white;
      position: relative;
    }
  
  @keyframes fanao {
      50%{ transform: scale(1.03, 1.01);}
      100%{ transform: scale(1, 1);}
    }
  
    .container_box_parent_fournisseur:hover{
      animation-name: fanao;
      animation-duration: 1.5s;
      animation-iteration-count: infinite;
       }




/*/---------------------------- DEBUT TETE FEUILLE QUI GLISSE -------------------------/*/

.box_qui_glisse_fournisseur{
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    color: black;
    position: relative;
    overflow: hidden;
    font-size: 1.3em;
    border-radius: 5px;
}

.box_qui_glisse_fournisseur span{
    display: inline-block;
    transition: transform 0.5s ease-out;
 }
 .spa{
    padding: 15px;
    width: 310px;
    text-align: center;
    font-family: sans-serif;
    color: white;
}
 .box_qui_glisse_fournisseur span:nth-child(2){
     position: absolute;
     left: 100%;
     width: 100%;
     text-align: center;
     font-size: 0.8em;
     font-weight: bold;
     color: white;
 }
 .box_qui_glisse_fournisseur:hover span{
     transform: translateX(-100%);
 }
