/*/ apres commun fournisseurs on fait inscri/*/
.container_dans_le_marche_parent_inscripton{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 15vh;
    position: absolute;
    width: 100%;
    min-height: 92vh;
}
.container_bas_march{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;

}
.container_bas_nav_dans_le_marche{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
 }

.titre_img_fournisseur_marche{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: white;
    width: 100%
}
.titre_dans_le{
    font-size: 2vw;
    font-weight: 500;
    text-transform: uppercase;
    color: #0f63d6;
    font-family: 'Open Sans', sans-serif;
    height: 5rem;
    display: flex;
    align-items: center;
    margin-left: 10rem;
}
.btn_cherche_dans_le_marche{
    margin-top: 1rem;
    width: 110px;
    padding: 1rem;
    color: white;
    border: solid white 1px;
    border-radius: 3px;
    display: flex;
    justify-content: center;  
    background-color: #00398a;
    margin-right: 3rem;
 }
 .btn_text_btn_dans_le_marche{
    margin-right: 0.5rem;
 }
 ::placeholder{
     color: rgba(255, 255, 255, 0.678);
 }
.cherche_dans_le_marche{
    display: flex;
    justify-content: center;
    align-items: center;
}


.container_liste1_dans_le_marche{
    display: flex;
 /*/   width:50vw;/*/
    justify-content: space-around;
    margin-top: 2rem;
}
.con_titre1_dans_le_marche_referencessifac{
    width: 5vw;
    border: solid 2.5px white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    padding: 0.8rem;
    margin-top:1rem ;
    background-color: #00398a;
    color: white;
    font-size: 0.9vw;
}
.con_titre1_dans_le_marche_numerodeposte{
    width: 15vw;
    border: solid 2.5px white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    padding: 0.8rem;
    margin-top:1rem ;
    background-color: #00398a;
    color: white;
    font-size: 0.9vw;
}
