.container_accueil_parent_contact{
  display: flex;
  flex-direction: column;
  margin-top: 8vh;
  position: absolute;
  width: 100%;
  height: 100%;
}

.container_bas_nav_contact{
  display: flex;
  justify-content: space-around;
  background-color: white;
  margin-top: 4rem;
  align-items: center;  
}

  .container_box_parent_contact{
    width: 22em;
    margin: 1rem;
    height: 22rem;
    border-radius: 5px;
    background-image: url("../images/fond_back_2.png");
    background-repeat: no-repeat;
    background-size: cover;
    border: 0.5px solid white;
    position: relative;
  }
  .container_box_parent_contact_click{
      width: 22em;
      margin: 1rem;
      height: 3.5rem;
      border-radius: 5px;
      background-image: url("../images/fond_back_2.png");
      background-repeat: no-repeat;
      background-size: cover;
      border: 0.5px solid white;
      position: relative;
    }
  
  @keyframes fanao {
    50%{ transform: scale(1.03, 1.01);}
    100%{ transform: scale(1, 1);}
  }
  .container_box_parent_contact:hover{
    animation-name: fanao;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
     }


/*/ PARTIE QUI GLISSE/*/
.box_qui_glisse_contact{
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  color: black;
  position: relative;
  overflow: hidden;
  font-size: 1.3em;
  border-radius: 5px;
}

.box_qui_glisse_contact span{
  display: inline-block;
  transition: transform 0.5s ease-out;
}
.spa{
  padding: 15px;
  width: 310px;
  text-align: center;
  font-family: sans-serif;
  color: white;
}
.box_qui_glisse_contact span:nth-child(2){
   position: absolute;
   left: 100%;
   width: 100%;
   text-align: center;
   font-size: 0.8em;
   font-weight: bold;
   color: white;
}
.box_qui_glisse_contact:hover span{
   transform: translateX(-100%);
}

  .box_container_contact{
    display: flex;
    margin-bottom: 0.2rem;
    margin-right: 1rem;
  }
  .box_lign1_contact{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    justify-content: space-between;
  }
  .text_icon_contact{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 0.6rem;
    width: 5rem;
    color: white;
  }
  .fa-house-user,
  .fa-user-tie,
  .fa-envelope-open,
  .fa-address-card{
    margin-left: 0.3rem;
    color: white;
  }


  .point_double_contact{
    color: white;
    font-weight: bold;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
  }
  .result_1_contact{
    width: 15rem;
    font-size: 1rem;
    color: white;
    word-wrap: break-word;
  }

  .container_contact{
    width: 70vw;
    margin: auto;
    margin-top: 2rem;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    align-items: center; 
  }
  .container_footer_contact{
    margin-top: 5rem;
    background-image: url("../images/footer_2.png");
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
}

.container_contact_footer{
  display: flex;
  justify-content: center;
  align-items: center;

  z-index: 2;
}

.footer_image_fond_contact{
  height: 15vh;
  width: 100%;
  position: absolute;
  bottom: 0rem;

}
.titre_img_contact{
  display: flex;
  justify-content: center;
  align-items: center;
}
.titre_C{
  font-size: 2vw;
  font-weight: 500;
  text-transform: uppercase;
  color: #0f63d6;
  font-family: 'Open Sans', sans-serif;
  height: 5rem;
  display: flex;
  align-items: center;
}


.deco_bar_contact{
  height: 8vh;
  margin-left: 5vw;
}
.contact_supre{
  position: absolute;
  right: 0.8rem;
  bottom: 0.7rem;
}
.contact_container_bas_feuille{
  position: relative;
}
