.container_premier{
    display: flex;
    flex-direction: column;
    margin: auto;
}

.contanav1{
    height: 100%;
    width: 100%;
   /*/ background-image: url("../images/nav_fond.png");/*/
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    z-index: -1;
}
.menu_logo_pet{
    display: none;
}
.logo_p{
    width: 40px;
    height: 40px;
}
.nav_image_fond{
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0rem;
    z-index: -1;
}
.nav_connecte{
    display: flex;
    justify-content: center;
    height: 13vh;
    display: flex;
    align-items: center;
}
.nav_connecte_2{
    width: 50vw;
    display: flex;
    justify-content: space-around;
    align-items: center;  
    z-index: 2;
}


.btn_loug_out_container{
    margin-left: 10rem;
    display: flex;
    justify-content: center;
    align-items: center; 
}
.btn_log_out{
    text-decoration: none;
    font-size: 1.3rem;
    color: white;
    font-family: 'Open Sans', sans-serif;

}

.btn_loug_out_petit_ecran{
    display: none;
}

.fa-sign-out-alt{
    font-size: 1.5rem;
    margin-left: 0.8rem;
    color: white;
    -webkit-transition:   -webkit-transform 1s;
}
.fa-sign-out-alt:hover{
   -webkit-transform: rotate(180deg) translateZ(0);
}

/*/ partie nav pas connecté/*/
.listenav3{
    margin-top: 3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.nav_inc3,
.nav_inc2,
.nav_inc1{
    text-decoration: none;
    font-size: 1.3rem;
    color: white;
    font-family: 'Open Sans', sans-serif; 
    position: relative;
}
.nav_inc_p{
    display: flex;
    align-items: center;
}
.nac_inc_p3{
    display: flex;
    margin-left: 3rem;
    align-items: center;
}

.nav_connecte_3{
    color: white; 
    font-family: 'Open Sans', sans-serif;
     font-size: 1.5rem; 
     line-height: 32px;  text-align: center;
     position: relative;
     text-decoration: none;
}

.nav_connecte_3::before{
    content: '';
    width: 100%;
    position: absolute;
    transform-origin: 100% 0 ;
    transform: scaleX(0);
    transition: transform 0.5s ease-in-out; 
    background-color: white;
    height: 1px;
    bottom: 0;
}

.nav_connecte_3:hover::before{
    transform: scaleX(1);
    transform-origin: 0 0 ;
}

.nav_inc1::before{
    content: '';
    width: 100%;
    position: absolute;
    transform-origin: 100% 0 ;
    transform: scaleX(0);
    transition: transform 0.5s ease-in-out; 
    background-color: white;
    height: 1px;
    bottom: 0;
}

.nav_inc1:hover::before{
    transform: scaleX(1);
    transform-origin: 0 0 ;
}
.nav_inc2::before{
    content: '';
    width: 100%;
    position: absolute;
    transform-origin: 100% 0 ;
    transform: scaleX(0);
    transition: transform 0.5s ease-in-out; 
    background-color: white;
    height: 1px;
    bottom: 0;
}

.nav_inc2:hover::before{
    transform: scaleX(1);
    transform-origin: 0 0 ;
}

.nav_inc3::before{
    content: '';
    width: 100%;
    position: absolute;
    transform-origin: 100% 0 ;
    transform: scaleX(0);
    transition: transform 0.5s ease-in-out; 
    background-color: white;
    height: 1px;
    bottom: 0;
}

.nav_inc3:hover::before{
    transform: scaleX(1);
    transform-origin: 0 0 ;
}

