/*/ apres commun fournisseurs on fait inscri/*/
.container_nacre_parent_inscripton{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-top: 15vh;
    position: absolute;
    width: 100%;
    min-height: 92vh;
}
.container_btn_bar{
    width: 100%;
}
