    margin-top: 1rem;
    width: 110px;
    padding: 1rem;
    color: white;
    border: solid white 1px;
    border-radius: 3px;
    display: flex;
    justify-content: center;  
    background-color: #00398a;
    margin-right: 3rem;
 }
