/*/ PARTIE COMMENTAIRE INPUT /*/
.inventaire2_container_cherche{
    width: 12vw;
    position: relative;
    height: 30px;
    overflow: hidden;
    margin-top: 15px;
}

.iventaire2_input_text{
  background-color: #0f62d604;
  font-weight: bold;
  margin-left: 35px;
  font-size: 1.3em;
  color: black;
}

.inventaire2_container_cherche input{
    width: 100%;
    height: 100%;
    color: white;
    border: none;
    outline: none;
} 

.inventaire2_container_cherche label {
    position: absolute;
    bottom: -3px;
    left: 0%;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 3.5px solid white;
}

.inventaire2_container_cherche label::after{
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    left: -1px;
    bottom: 0px;
    border-bottom: 2px solid white;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
}

.icon_commentaire_input{
    position: absolute;
    bottom: 5px;
    left: 10px;
    transition: all 0.3s ease;
    color: rgba(255, 255, 255, 0.5);
}
::placeholder{
    color: rgba(255, 255, 255, 0.418);
}
.inventaire2_container_cherche input:focus +.inventaire2_le_label .icon_iventaire2_input_text , 
.inventaire2_container_cherche input:valid + .inventaire2_le_label .icon_iventaire2_input_text {
    transform: translateY(-70%) translateX(-30%);
    font-size: 0.8em;
    color: white;
}

.inventaire2_container_cherche input:focus +.inventaire2_le_label::after ,.inventaire2_container_cherche input:valid + .inventaire2_le_label::after{
    transform: translateX(0%);
}
.inventaire2_le_label{
    display: flex ;
    align-items: center;

}
