.container_logine_gmao{
    top: 8vh;
    position: absolute;
    width: 100%;
}

.contine_logine_bar{
    margin-bottom: 3rem;
    background-color: white;
    display: flex;
}

.titre_img_logine{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 8vw;
  }

  .deco_bar_logine{
    height: 8vh;
    margin-left: 8.5vw;
  }
  .titre_logine{
    font-size: 2vw;
    font-weight: 500;
    text-transform: uppercase;
    color: #0f63d6;
    font-family: 'Open Sans', sans-serif;
    height: 5rem;
    display: flex;
    align-items: center;
  }
  .logine_pass_oublier{
      margin-top: 2rem;
      color: white;
      display: flex;
      justify-content: center;
  }
  .logine_pass_oublier:hover{
    font-weight: bold;
  }

  .soratra_log{
    height: 10vw;
    width: 10vw;
    position: absolute;
    bottom: 20rem;
    left: 7rem;
  }
  @media screen and (max-width : 1000px){
    .soratra_log{
      display: none;
    }
  }
